<template>
  <div class="textBox">
    <transition>
    <h1 v-if="item(index).toggle === true" @click="toggleTool(index)">
      {{this.item(this.index).title}}
    </h1>
    <div v-if="item(index).toggle === false">
      <button @click="toggleTool(index)">취소</button>
      <input class="edit-title" type="text" v-model="item(index).title"/>
    </div>
    </transition>
    <input type="text" readonly/>
  </div>
</template>

<script>
import RenderCommBox from './RenderCommBox'
export default {
  name: 'RenderTextBox',
  mixins: [RenderCommBox],
  computed: {
    /*
    value: {
      get: () => this.item[this.index].value,
      set (val) { this.item[this.index].value = val }
    }
    */
  }
}
</script>

<style scoped>
.textBox {
  width: 500px;
  margin: 20px;
}
.textBox > input{
  width: 400px;
  height: 30px;
  font-family: inherit;
  padding: 10px;
}

.textBox > div > input.edit-title {
  width: 400px;
  height: 30px;
  font-family: inherit;
  padding: 10px;
  border: none;
  margin: 10px;
}
</style>



